<template>
  <div class="purchase-order-container">
    <el-card>
      <div slot="header" class="clearfix">
        <span>采购订单管理</span>
        <el-button type="primary" size="small" @click="handleAdd" style="float: right">新增采购订单</el-button>
      </div>

      <!-- 查询条件 -->
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item label="订单编号">
          <el-input v-model="searchForm.orderNumber" placeholder="请输入订单编号"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch">查询</el-button>
          <el-button @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
      <!-- 表格数据 -->
      <el-table :data="tableData" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column prop="orderNumber" label="订单编号"></el-table-column>
        <el-table-column prop="supplierName" label="供应商"></el-table-column>
        <el-table-column prop="totalAmount" label="订单金额" :formatter="formatCurrency"></el-table-column>
        <el-table-column prop="status" label="订单状态">
          <template slot-scope="scope">
            <el-tag :type="getStatusType(scope.row.status)">{{ getStatusText(scope.row.status) }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="描述"></el-table-column>
        <el-table-column label="操作" width="200" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" v-if="scope.row.status === 0" @click="handleEdit(scope.row)">编辑</el-button>
            <el-button size="small" type="danger" v-if="scope.row.status === 0" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="pagination-container">
        <el-pagination
          background
          :current-page="currentPage"
          :page-size="pageSize"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
      <!-- 批量操作 -->
      <div v-if="selectedRows.length > 0" class="batch-operation">
        <el-button type="danger" @click="handleBatchDelete">批量删除</el-button>
      </div>
    </el-card>
    <!-- 订单表单对话框 -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="80%">
      <el-form :model="form" :rules="rules" ref="orderForm" label-width="100px">
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="订单编号" prop="orderNumber">
              <el-input v-model="form.orderNumber" placeholder="自动生成" readonly></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="供应商" prop="supplierId">
              <el-select style="width: 100%;" v-model="form.supplierId" placeholder="请选择供应商">
                <el-option v-for="supplier in supplierList" :key="supplier.id" :label="supplier.supplierName" :value="supplier.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="订单金额" prop="totalAmount">
              <el-input v-model.number="form.totalAmount" placeholder="请输入订单金额"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="订单状态" prop="status">
              <el-select style="width: 100%;" v-model="form.status" placeholder="请选择订单状态">
                <el-option label="草稿" :value="0"></el-option>
                <el-option label="已提交" :value="1"></el-option>
                <el-option label="已审核" :value="2"></el-option>
                <el-option label="已完成" :value="3"></el-option>
                <el-option label="已取消" :value="4"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="描述" prop="remark">
              <el-input v-model="form.remark" placeholder="请输入订单描述" type="textarea" :rows="4"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel">取消</el-button>
        <el-button type="primary" @click="handleSubmit">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {purchaseOrderAPI, supplierAPI} from "@/api/api";

export default {
  name: 'PurchaseOrder',
  data() {
    return {
      // 查询条件
      searchForm: {
        orderNumber: '',
        supplierId: '',
        status: '',
        startDate: '',
        endDate: ''
      },
      // 表格数据
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      // 选中的行
      selectedRows: [],
      // 对话框
      dialogVisible: false,
      dialogTitle: '',
      // 供应商列表
      supplierList: [],
      // 表单数据
      form: {
        id: null,
        orderNumber: '',
        supplierId: '',
        totalAmount: 0,
        description: '',
        status: 0
      },
      // 表单验证规则
      rules: {
        orderNumber: [
          { required: true, message: '订单编号不能为空', trigger: 'blur' }
        ],
        supplierId: [
          { required: true, message: '请选择供应商', trigger: 'blur' }
        ],
        orderDate: [
          { required: true, message: '请选择订单日期', trigger: 'blur' }
        ]
      },
      // 当前用户信息
      currentUser: {
        id: 1,
        companyId: 1
      }
    }
  },
  mounted() {
    this.loadSupplierList()
    this.loadData()
  },
  methods: {
    // 加载供应商列表
    loadSupplierList() {
      supplierAPI.getList().then(res => {
        if (res.code === 200) {
          this.supplierList = res.data
        }
      })
    },

    // 加载数据
    loadData() {
      const params = {
        pageNum: this.currentPage,
        pageSize: this.pageSize,
        ...this.searchForm
      }

      purchaseOrderAPI.page(params).then(res => {
        if (res.code === 200) {
          this.tableData = res.data.records
          this.total = res.data.total
          // 关联供应商名称
          this.tableData.forEach(order => {
            const supplier = this.supplierList.find(s => s.id === order.supplierId)
            if (supplier) {
              order.supplierName = supplier.supplierName
            }
          })
        } else {
          this.$message.error(res.message)
        }
      }).catch(err => {
        this.$message.error('加载数据失败')
      })
    },

    // 查询
    handleSearch() {
      this.currentPage = 1
      this.loadData()
    },

    // 重置
    handleReset() {
      this.searchForm = {
        orderNumber: '',
        supplierId: '',
        status: ''
      }
      this.loadData()
    },

    // 分页大小改变
    handleSizeChange(size) {
      this.pageSize = size
      this.loadData()
    },

    // 当前页改变
    handleCurrentChange(current) {
      this.currentPage = current
      this.loadData()
    },

    // 选择行变化
    handleSelectionChange(rows) {
      this.selectedRows = rows
    },

    // 新增
    handleAdd() {
      this.dialogTitle = '新增采购订单'
      this.generateOrderNumber().then(orderNumber => {
        this.form = {
          id: null,
          orderNumber: orderNumber,
          supplierId: '',
          totalAmount: 0,
          description: '',
          status: 0
        }
        this.dialogVisible = true
      })
    },

    // 编辑
    handleEdit(row) {
      this.dialogTitle = '编辑采购订单'
      this.form = {...row};
      this.dialogVisible = true
    },

    // 查看
    handleView(row) {
      this.dialogTitle = '查看采购订单'
      purchaseOrderAPI.getById(row.id).then(res => {
        if (res.code === 200) {
          this.form = {
            id: res.data.id,
            companyId: res.data.companyId,
            supplierId: res.data.supplierId,
            amount: res.data.amount,
            description: res.data.description,
            status: res.data.status
          }
          this.dialogVisible = true
          // 禁用表单
          this.$nextTick(() => {
            this.$refs.orderForm.fields.forEach(field => {
              field.disabled = true
            })
          })
        } else {
          this.$message.error(res.message)
        }
      })
    },

    // 删除
    handleDelete(id) {
      this.$confirm('确定要删除该采购订单吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        purchaseOrderAPI.delete(id).then(res => {
          if (res.code === 200) {
            this.$message.success('删除成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('删除失败')
        })
      })
    },

    // 批量删除
    handleBatchDelete() {
      const ids = this.selectedRows.map(row => row.id)
      this.$confirm(`确定要删除选中的 ${ids.length} 个采购订单吗？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        purchaseOrderAPI.deleteBatch(ids).then(res => {
          if (res.code === 200) {
            this.$message.success('批量删除成功')
            this.loadData()
            this.selectedRows = []
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('批量删除失败')
        })
      })
    },

    // 提交
    handleSubmitOrder(id) {
      this.$confirm('确定要提交该采购订单吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        purchaseOrderAPI.submit(id).then(res => {
          if (res.code === 200) {
            this.$message.success('提交成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('提交失败')
        })
      })
    },

    // 审核
    handleApprove(id) {
      this.$confirm('确定要审核该采购订单吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        purchaseOrderAPI.approve(id).then(res => {
          if (res.code === 200) {
            this.$message.success('审核成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('审核失败')
        })
      })
    },

    // 取消
    handleCancelOrder(id) {
      this.$confirm('确定要取消该采购订单吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        purchaseOrderAPI.cancel(id).then(res => {
          if (res.code === 200) {
            this.$message.success('取消成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('取消失败')
        })
      })
    },

    // 取消对话框
    handleCancel() {
      this.dialogVisible = false
      this.$refs.orderForm.resetFields()
      // 启用表单
      this.$refs.orderForm.fields.forEach(field => {
        field.disabled = false
      })
    },

    // 提交表单
    handleSubmit() {
      this.$refs.orderForm.validate((valid) => {
        if (valid) {
          const params = {
            ...this.form,
          }

          if (this.form.id) {
            // 编辑
            purchaseOrderAPI.update(params).then(res => {
              if (res.code === 200) {
                this.$message.success('更新成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('更新失败')
            })
          } else {
            // 新增
            purchaseOrderAPI.add(params).then(res => {
              if (res.code === 200) {
                this.$message.success('新增成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('新增失败')
            })
          }
        }
      })
    },

    // 添加明细行
    addItem() {
      this.form.items.push({
        productCode: '',
        productName: '',
        productSpec: '',
        unit: '',
        quantity: 0,
        unitPrice: 0,
        amount: 0,
        taxRate: 13,
        taxAmount: 0
      })
    },

    // 删除明细行
    removeItem(index) {
      this.form.items.splice(index, 1)
      this.updateTotalAmount()
    },

    // 更新明细金额
    updateItemAmount(index) {
      const item = this.form.items[index]
      item.amount = (item.quantity || 0) * (item.unitPrice || 0)
      this.updateItemTax(index)
      this.updateTotalAmount()
    },

    // 更新税额
    updateItemTax(index) {
      const item = this.form.items[index]
      item.taxAmount = item.amount * (item.taxRate || 0) / 100
    },

    // 更新总金额
    updateTotalAmount() {
      let total = 0
      this.form.items.forEach(item => {
        total += item.amount || 0
      })
      this.form.totalAmount = total
    },

    // 生成订单编号
    generateOrderNumber() {
      return new Promise((resolve) => {
        purchaseOrderAPI.generateNumber({companyId: 1}).then(res => {
          if (res.code === 200) {
            resolve(res.data)
          } else {
            resolve('PO' + new Date().getTime()) // 备用方案
          }
        }).catch(() => {
          resolve('PO' + new Date().getTime()) // 备用方案
        })
      })
    },

    // 格式化日期
    formatDate(row, column, cellValue) {
      if (!cellValue) return ''
      return new Date(cellValue).toLocaleDateString()
    },

    // 格式化货币
    formatCurrency(row, column, cellValue) {
      if (!cellValue) return '0.00'
      return Number(cellValue).toFixed(2)
    },

    // 获取状态文本
    getStatusText(status) {
      const statusMap = {
        0: '草稿',
        1: '已提交',
        2: '已审核',
        3: '已完成',
        4: '已取消'
      }
      return statusMap[status] || ''
    },

    // 获取状态标签类型
    getStatusType(status) {
      const typeMap = {
        0: 'info',
        1: 'primary',
        2: 'success',
        3: 'warning',
        4: 'danger'
      }
      return typeMap[status] || 'info'
    },

    // 单元格点击事件
    handleCellClick(row, column, cell, event) {
      // 可以在这里实现双击编辑等功能
    }
  }
}
</script>

<style scoped>
.purchase-order-container {
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
}

.pagination-container {
  margin-top: 20px;
  text-align: right;
}

.batch-operation {
  margin-top: 10px;
}
</style>
